<template>
<div>
<nav class="nav_tabs">
        <ul>
            <li>
                <input type="radio" name="tabs" class="rd_tabs" id="tab1" checked>
                <label for="tab1">Em Aberto</label>
                <div class="content">
                    <div class="container">
                        <div class="row">
                            <table class="table-vertical">
                                <thead>
                                    <tr>
                                        <th>Parcela</th>
                                        <th>Vencimento</th>
                                        <th>Valor líquido</th>
                                        <th>Valor atual</th>
                                        <th>Opções</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <tr v-for="item in mensalidadeAPagar" :key="item.id">
                                            <td data-th="Parcela">{{item.parcela}}</td>
                                            <td data-th="Vencimento">{{item.vencimento | formatDate }}</td>
                                            <td data-th="Valor líquido">{{item.valorLiquido}}</td>
                                            <td data-th="Valor atual">{{item.valorAtual}}</td>
                                            <td data-th="Opções"><router-link :to="{name: 'OpcaoPagamento'}">
                                                            <button id="botao-pagar" class='form-control btn btn-primary submit-button' v-on:click="realizarPagamento(item)">Realizar pagamento</button>         
                                                            </router-link></td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </li>
            <li>
                <input type="radio" name="tabs" class="rd_tabs" id="tab2">
                <label for="tab2">Pago</label>
                <div class="content">
                    <div class="container">
                        <div class="row">
                            <table class="table-vertical">
                                <thead>
                                    <tr>
                                        <th>Parcela</th>
                                        <th>Vencimento</th>
                                        <th>Valor líquido</th>
                                        <th>Valor pago</th>
                                        <th>Data de Pagamento</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="item in mensalidadePagas" :key="item.id">
                                            <td data-th="Parcela">{{item.parcela}}</td>
                                            <td data-th="Vencimento">{{item.vencimento | formatDate}}</td>
                                            <td data-th="Valor líquido">{{item.valorLiquido}}</td>
                                            <td data-th="Valor pago">{{item.valorPago}}</td>
                                            <td data-th="Data pagamento">{{item.dataPagamento| formatDate}}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </li>
        </ul>
    </nav>
        <div class="button">
            <router-link :to="{name:'MenuInicial'}">
            <a class="botao" id="btn1">
                <i class="fa fa-home"></i><br>
                <span>Início</span><br>
            </a>
        </router-link>
        </div>
</div>
</template>
<script>
import {
  FETCH_MONTHLY_PAYMENTS,
  UPDATE_PAYMENT
} from "./../store/action-type";
import { mapGetters } from "vuex";
import router from '../router'
export default {
  name: 'Mendalidade',
  data: () => ({
    
  }),
  mounted() {
    if(!this.isLoggedIn){
        return router.push('/Login');
    }
    this.fetchMothlyPayments();
    
  },
  computed: {
    ...mapGetters(["mensalidades", "isLoggedIn"]),
    mensalidadePagas: function() {
        return this.mensalidades.filter(function(u) {
            return u.pagamentoRealizado
        })
    },
    mensalidadeAPagar: function() {
        return this.mensalidades.filter(function(u) {
            return !u.pagamentoRealizado
        })
    }     
  },
  methods: {
    fetchMothlyPayments() {
      this.$store.dispatch(FETCH_MONTHLY_PAYMENTS, this.$store.state.authorization.aluno.matricula);
    },
    realizarPagamento(mensalidade){
        this.$store.dispatch(UPDATE_PAYMENT, mensalidade);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
*{
    padding: 0;
    margin: 0;
}
body{
    background-color: #fff;
    font-family: sans-serif;

    text-decoration:none;
    text-transform: uppercase;
    letter-spacing: 2px;
}

.nav_tabs{
    width: 900px;
    height: 450px;
    margin: 60px auto;
    background-color: #ecf0f1;
    position: relative;
    box-shadow: 0 5px 15px -5px #00000070;
}
.nav_tabs ul{
    list-style: none;
}
.nav_tabs ul li{
    float: left;
}
.nav_tabs label{
    width: 400px;
    padding: 25px;
    background-color: #95a5a6;
    display: block;
    color: #fff;
    cursor: pointer;
    text-align: center;
}
.rd_tabs:checked ~ label{
    background-color: #008200;
}
.rd_tabs{
    display: none;
}

.content{
    background-color: #ecf0f1;
    display: none;
    position: absolute;
    height: 382px;
    width: 900px;
    left: 0;
}


.button{
    position: absolute;
    top: 90%;
    transform: translateY(-50%);
    width: 100%;
    text-align: center;
    text-decoration: none;
}
.botao{
    display: inline-block;
    width: 100px;
    height: 100px;
    background: #f1f1f1;
    margin: 10px;
    border-radius: 20%;
    box-shadow: 0 5px 15px -5px #00000070;
    overflow: hidden;
    position: relative;
    text-decoration:none;
}
.botao span {
    font-family: sans-serif;
    color:  #008200;
    text-decoration:none;
    text-transform: uppercase;
    letter-spacing: 2px;
}
.botao i{
    color:  #008200;
    line-height: 320%;
    font-size: 20px;
    transition: 0.2s linear;
}
.botao:active{
    transform: translate(2px, 2px);
}
.fa, .fas {
    font-size: 21px;
}
#botao-pagar{
    background-color:#008200;
    font-family: sans-serif;
    text-decoration:none;
    text-transform: uppercase;
    color: #f9f9f9;
    padding: 5px;
    font-size: 12px;    
    border-radius: 8%;
    height: 35px;
}

.rd_tabs:checked ~ .content{
    display: block;
}

/* 
tabela
*/
.table-vertical { 
    width: 100%; 
    height:auto;
    border-collapse: collapse; 
  }
  /* Zebra striping */
  .table-vertical>tbody>tr:nth-of-type(odd) { 
    background: #f9f9f9;
  }
  .table-vertical>thead>tr>th { 
    background: #fff; 
    color: #000; 
    font-weight: bold; 
  }
  .table-vertical>tbody>tr>td, .table-vertical>thead>tr>th { 
    padding: 12px; 
    border-top: 1px solid #ddd; 
    text-align: center; 
    vertical-align: middle;
  }
  
  /* 
  Max width before this PARTICULAR table gets nasty
  This query will take effect for any screen smaller than 760px
  and also iPads specifically.
  */
  @media 
  only screen and (max-width: 760px),
  (min-device-width: 768px) and (max-device-width: 1024px)  {
  
      /* Force table to not be like tables anymore */
      table, thead, tbody, th, td, tr{ 
          display: block; 
      }
      
      /* Hide table headers (but not display: none;, for accessibility) */
      .table-vertical thead tr { 
          position: absolute;
          top: -9999px;
          left: -9999px;
      }
      
    
    .button {
        position: absolute;
          /* Top/left values mimic padding */
        top: 1130px;
        left: 160%;
        width: 45%; 
        padding-right: 10px; 
        white-space: nowrap;
    }

      .table-vertical>thead>tr,  .table-vertical>tbody>tr{ border: 1px solid #ccc; }
      
       
      .table-vertical>thead>tr>td,  .table-vertical>tbody>tr>td{ 
          /* Behave  like a "row" */
          border: none;
          border-bottom: 1px solid #eee; 
          position: relative;
          padding-left: 50%; 
      }
      
      .table-vertical>tbody>tr>td:before { 
          /* Now like a table header */
          position: absolute;
          /* Top/left values mimic padding */
          top: 6px;
          left: 6px;
          width: 45%; 
          padding-right: 10px; 
          white-space: nowrap;
      }
      
      /*
      Label the data
      */
      .table-vertical>tbody>tr>td:before { content: attr(data-th); }
      
      #btn0 {
           background: #008200;
      }
      
  }
</style>



