<template>
<div>
  <nav class="nav">
 <ul>
  <li>
  <label id="header">Pagamento de mensalidade</label>
    <div class="content">
      <div class="container">
        <div class="mb-5">
          <div id="accordion" role="tablist">
            <div class="block mb-3">
              <div id="headingOne" role="tab" class="block-header"><strong>Cartão</strong>
              </div>
              <div id="collapseOne" role="tabpanel" aria-labelledby="headingOne" data-parent="#accordion"
                class="collapse show" style="">
                <div class="block-body">
                  <form @submit="realizarPagamento">
                    <div class="row">
                      <div class="form-group col-md-6">
                        <label for="nome-cartao" class="form-label">Nome do titular </label>
                        <input type="text" v-model="nomeAluno" required  v-uppercase placeholder="Igual ao descrito no cartão"
                          class="form-control">
                      </div>
                      <div class="form-group col-md-4">
                        <label for="nome-bandeira" class="form-label">Operadora</label>
                        <v-select :options="['MasterCard', 'Visa', 'Elo']"  v-model="operadora" required placeholder="Selecione a operadora"></v-select>
                      </div>
                      <div class="form-group col-md-6">
                        <label for="num-cartao" class="form-label">Número do cartão</label>
                        <input type="" v-model="numeroCartao" required v-mask="'#### #### #### ####'" placeholder="9999 9999 9999 9999"
                          class="form-control">
                      </div>
                      <div class="form-group col-md-4">
                        <label for="validade" class="form-label">Data de validade</label>               
                        <input type="text" v-model="dataValidade" required v-mask="'##/##'" placeholder="MM/AA" class="form-control">
                      </div>
                      <div class="form-group col-md-4">
                        <label for="cvv" class="form-label">Código de segurança</label>
                        <input type="password" v-model="codigoSeguranca" required name="cvv" placeholder="123" id="cvv" class="form-control">
                    </div>
                      </div>
                      <div class="form-group col-md-3">
                        <label for="total">Valor: </label>
                        <span class='amount' >R${{mensalidade.valorAtual}}</span>
                        <button id="botao-pagar" class='form-control btn btn-primary'>Pagar »</button>
                      </div>
                  </form>
                </div>
              </div>
            </div>
