      </div>
      </li>
      </ul>
  </nav>

  <div class="button">
      <router-link :to="{name:'OpcaoPagamento'}">
        <a class="botao" id="btn1">
            <i class="fa fa-angle-left"></i><br>
            <span>Voltar</span><br>
        </a>
    </router-link>
    <router-link :to="{name:'MenuInicial'}">
        <a class="botao" id="btn1">
