<template>
        <div class="middle">
          <img src="./../assets/facear.jpg" alt="logo" class="avatar">
            
            <router-link :to="{name: 'Mensalidade'}">
            <a class="btn" id="btn1">
                <i class="fa fa-chart-pie"></i><br>
                <span>Mensalidades</span>
            </a>
            </router-link>
            <router-link :to="{name: 'Requerimento'}">
            <a class="btn" id="btn3">
                <i class="fa fa-file-alt"></i><br>
                <span>Requerimentos</span>
            </a>
            </router-link>
            <router-link :to="{name: 'Estudante'}">
            <a class="btn" id="btn4" href="#">
                <i class="fa fa-graduation-cap"></i><br>
                <span>Estudante</span>
            </a>
            </router-link>
            <div class="button">
                  <a class="botao" id="btn0"  v-on:click="Logout($event)">
                  <i class="fas fa-door-open"></i><br>
                  <span>Sair</span><br>
                  </a>
            </div>
        </div>
</template>
<script>
import { LOGOUT } from '../store/action-type';
import { mapGetters } from 'vuex';
import router from '../router'
export default {
  name: 'MenuInicial',
  data: () => ({
    
  }),
  mounted(){
      if(!this.isLoggedIn){
          return router.push('/Login');
      }
  },
  computed: {
    ...mapGetters(["isLoggedIn"])
  },
  methods: {
    Logout : function (e) {
      e.preventDefault();
      this.$store.dispatch(LOGOUT);
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.avatar{
    width: 200px;
    height: 135px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
}
.middle{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
    text-align: center;
}
.btn{
    display: inline-block;
    width: 200px;
    height: 200px;
    background: #f1f1f1;
    margin: 10px;
    border-radius: 20%;
    box-shadow: 0 5px 15px -5px #00000070;
    overflow: hidden;
    position: relative;
    text-decoration:none;
}
.btn span {
    font-family: sans-serif;
    color: #fff;
    text-decoration:none;
    text-transform: uppercase;
    letter-spacing: 2px;
}
.btn i{
    color: #fff;
    line-height: 320%;
    font-size: 40px;
    transition: 0.2s linear;
}
.btn:active{
    transform: translate(2px, 2px);
}
#btn1{
    background: #008200;
}
#btn2{
    background: #ff9900;
}
#btn3{
    background: #ff9900;
}
#btn4{
    background: #7d3f98;
}
.botao{
    display: inline-block;
    width: 100px;
    height: 100px;
    background: #f1f1f1;
    margin: 10px;
    border-radius: 20%;
    box-shadow: 0 5px 15px -5px #00000070;
    overflow: hidden;
    position: relative;
    text-decoration:none;
}
.botao span {
    font-family: sans-serif;
    color:  #7dc300;
    text-decoration:none;
    text-transform: uppercase;
    letter-spacing: 2px;
}
.botao i{
    color:  #7dc300;
    line-height: 320%;
    font-size: 20px;
    transition: 0.2s linear;
}
.botao:active{
    transform: translate(2px, 2px);
}
.rd_tabs:checked ~ .content{
    display: block;
}

</style>



